html {
	scroll-behavior: smooth; }

body {
	margin: 0;
	padding: 0; }

* {
	box-sizing: border-box; }

.container {
	height: 100vh;
	background: rgb(242, 235, 232) {
  	position: relative; } }


.main_text {
	text-align: center;
	position: absolute;
	top: 40%;
	left: 23%; }

.main_text h1 {
	font-family: 'Bad Script', cursive;
	font-size: 60px;
	letter-spacing: 4px;
	z-index: 100; }

.button_country {
	text-align: center;
	position: absolute;
	top: 65%;
	left: 40%;
	cursor: pointer;
	border: 1px solid;
	border-radius: 35px;
	padding: 0 30px;
	width: auto; }

.button_country h2 {
	font-family: "Lora", serif;
	font-size: 25px; }



.main_text a {
	text-decoration: none;
	color: black; }

.machu {
	width: 320px;
	transform: rotate(-16deg);
	position: absolute;
	top: -40px; }

.mar {
	width: 340px;
	position: absolute;
	top: -5%;
	left: 35%;
	transform: rotate(7deg); }

.plane {
	width: 90px;
	position: absolute;
	top: 540px;
	left: 47%;
	z-index: 100; }


.spain {
	width: 70px;
	position: absolute;
	top: 48%;
	left: 15%; }

.jacaranda {
	width: 250px;
	transform: rotate(15deg);
	position: absolute;
	top: 28%;
	right: 0%; }


.alpaca {
	width: 250px;
	position: absolute;
	top: 0%;
	right: 15%; }

.avocado {
	width: 100px;
	position: absolute;
	top: 6%;
	left: 26%; }



.mate {
	width: 100px;
	position: absolute;
	right: 2%;
	top: 7%;
	transform: rotate(-3deg); }


.rio {
	width: 330px;

	transform: rotate(6deg);
	position: absolute;
	top: 400px;
	left: 10px; }

.italia {
	width: 300px;

	transform: rotate(-3deg);
	position: absolute;
	right: 50px;
	bottom: 40px; }

