//about_me

.grid {
	display: grid; }

.about_me {}


.about_me_pic {
	margin-top: 40px;
	background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3) ), url("../img/43.jpg");
	height: 250px;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	text-align: center;
	display: grid;
	align-items: center; }


.about_me_pic h1 {
	color: white;
	font-size: 40px;
	text-transform: uppercase;
	letter-spacing: 10px;
	font-weight: 700;
	font-family: Montserrat,sans-serif; }


.about_me_content {
	grid-template-columns: 1fr 1fr 1fr 1fr;
	justify-content: center;
	align-items: center;
	height: 60vh;
	& img {
		height: 170px;
		border-radius: 25px;
		margin-top: 25px; } }

.about_me_info {
	font-size: 20px;
	font-family: Lora,serif;
	text-align: center; }

.about_me_text {
	font-size: 16px;
	font-family: Lora,serif;
	text-align: center;
	color: #565050; }

.about_me_text h1 {
	font-size: 30px;
	font-family: Montserrat,sans-serif; }

.about_me_picture {
	text-align: center; }

.about_me_picture img {
	width: 80%; }

.my_fav {
	text-align: center;
	color: #565050;
	height: 80vh;
	background: rgba(247, 242, 238, 1); }

.my_fav h1 {
	font-family: 'Bad Script', sans-serif;
	padding-top: 20px; }
.my_fav a {
	color: #565050;
	text-decoration: none; }

.fav_articles {
	grid-template-columns: 1fr 1fr 1fr;
	margin: 20px;
	grid-gap: 10px;
	align-content: center; }

.fav_article {
	font-family: Montserrat,sans-serif;
	color: #565050;
	border: 1px solid #857c7c;
	overflow: hidden;
	text-align: center; }


.fav_article img {
	max-width: 350px;
	max-height: 230px;
	margin-top: 16px;
	transform: scale(1);
	transition: 1s;

	&:hover {
		transform: scale(1.1); } }



.fav_article h2 {
	font-size: 18px;
	font-family: Lora; }

.dogs_wrapper {

	text-align: center;
	height: 70vh;
	justify-content: center; }


.dogs_wrapper h1 {
	padding-top: 50px;
	font-family: 'Bad Script', sans-serif;
	color: #565050; }

.owl-next, .owl-prev {
  position: absolute;
  top: 30%; }

.owl-carousel {
	font-size: 40px; }

.owl-next {
  right: 2%; }


.owl-prev {
	left: 2%; }


.owl-nav {
	display: block !important; }

.owl-carousel .owl-prev:hover {
	background: #f7f2ee !important;
	color: black !important; }

.owl-carousel .owl-next:hover {
	background: #f7f2ee !important;
	color: black !important; }


.owl-carousel .owl-item {
	img {
		text-align: center;
		max-width: 300px !important;
		margin: auto; } }

.fav_dog img {
	max-width: 350px;
	max-height: 230px;
	margin-top: 16px;
	transform: scale(1);
	transition: 1s;

	&:hover {
		transform: scale(1.1); } }


// footer
.footer {
	grid-template-columns: 1fr 1fr 250px;
	justify-content: center;
	border-top: 1px dotted #eee;
	margin-top: 30px;
	margin-bottom: 40px; }


.footer_map img {
	max-width: 400px; }

.footer_map, .footer_social, .random {
	text-align: center;
	& h2 {
		font-family: Montserrat,sans-serif;
		color: #565050;
		font-size: 18px; }

	& p, a {
		font-family: Montserrat,sans-serif;
		color: #565050;
		text-decoration: none;
		margin: 20px; } }
.social_grid {
	font-family: Montserrat,sans-serif;
	color: #565050;
	justify-content: center;
	grid-template-columns: 50px 50px;
	margin-top: 50px;
	grid-gap: 10px;
	font-size: 29px; }

.random p {
	font-family: 'Caveat', cursive;
	font-size: 27px; }
